@font-face {
  font-family: "Helvetica Neue";
  src: url("../fonts/Helvetica Neue Regular.otf");
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Lato-Regular.ttf");
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Lato-Bold.ttf");
}
@font-face {
  font-family: "Arvo";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Arvo-Regular.ttf");
}
*,
::after,
::before {
  box-sizing: border-box;
}

body {
  font-family: Lato, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  font-style: normal;
  margin: 0;
  background: #FFF;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

.container {
  --bs-gutter-x: 1.5rem;
  --bs-gutter-y: 0;
  width: 100%;
  padding-right: calc(var(--bs-gutter-x) * 0.5);
  padding-left: calc(var(--bs-gutter-x) * 0.5);
  margin-right: auto;
  margin-left: auto;
}

.row {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 51px;
  --bs-gutter-x: 1.5rem;
  --bs-gutter-y: 0;
  flex-wrap: wrap;
  margin-top: calc(-1 * var(--bs-gutter-y));
  margin-right: calc(-0.5 * var(--bs-gutter-x));
  margin-left: calc(-0.5 * var(--bs-gutter-x));
}

.row > * {
  flex-shrink: 0;
  width: 100%;
  max-width: 100%;
  padding-right: calc(var(--bs-gutter-x) * 0.5);
  padding-left: calc(var(--bs-gutter-x) * 0.5);
  margin-top: var(--bs-gutter-y);
}

.wrapper a {
  cursor: pointer;
}
.wrapper .button {
  display: flex;
  width: 100%;
  padding: 20px 48px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 3px;
  text-align: center;
  font-size: 20px;
  font-weight: 700;
}
.wrapper .button.demo {
  border: 1px solid var(--border, #DDD);
  background: #FFF;
  color: var(--secondary-text, #777);
}
.wrapper .button.buy {
  border: 1px solid rgba(0, 0, 0, 0.2);
  background: var(--orange, #E27730);
  color: #FFF;
}
.wrapper .buttons {
  display: flex;
  align-items: flex-start;
  gap: 30px;
}

/* Header Section */
.header-section {
  position: relative;
  width: 100%;
  flex-shrink: 0;
  top: 0;
}
.header-section svg {
  width: 100%;
  height: initial;
}
.header-section .background {
  height: 660px;
  flex-shrink: 0;
  background-color: #F8F8F8;
}
.header-section .header-container {
  max-width: 635px;
}
.header-section .col-sm-no-padding {
  padding: 0;
}
.header-section .video {
  flex-shrink: 0;
  border-radius: 6px;
  background: #FFF;
  box-shadow: 0 10px 50px 0 rgba(0, 0, 0, 0.15);
  margin-right: auto;
  margin-left: auto;
  position: relative;
  top: 70px;
  width: 384px;
  height: 216px;
}
.header-section .video iframe {
  border: 0;
  width: 100%;
  height: 100%;
}
.header-section .logo {
  width: inherit;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
  top: 10px;
  margin-top: 40px;
}
.header-section .logo #title {
  height: 62px;
}
.header-section .logo #title .title {
  display: inline-flex;
  margin-right: 2px;
  color: var(--primary-text, #444);
  font-family: Arvo, sans-serif;
  font-size: 48px;
  font-style: normal;
  font-weight: 400;
  line-height: 59.27px;
}
.header-section .logo #title .pro-badge {
  display: inline-flex;
  padding: 5px 10px;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  float: right;
}
.header-section .logo .subtitle {
  color: var(--secondary-text, #777);
  text-align: center;
  font-size: 20px;
  line-height: 28px;
  opacity: 0.8;
}

/* Feature Section */
.features-section {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 250px;
  margin-bottom: 100px;
}
.features-section .row {
  margin-right: auto;
  margin-left: auto;
}
.features-section .row.bottom {
  margin-top: 51px;
}
.features-section .row .feature {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
.features-section .row .feature .title {
  color: var(--primary-text, #444);
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  line-height: 30px;
  margin: 6px auto;
}
.features-section .row .feature .description {
  color: var(--secondary-text, #777);
  text-align: center;
  font-size: 16px;
  line-height: 24px;
  margin: 8px auto;
}
.features-section .row .feature .icon {
  width: 48px;
  height: 48px;
}
.features-section .row .center-block {
  padding-left: 51px;
  padding-right: 51px;
}

/* Testimonials Section */
.testimonials-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 100px;
  background: linear-gradient(180deg, #F8F8F8 0%, #FFF 100%);
  padding-top: 100px;
  margin-bottom: 100px;
}
.testimonials-section .row {
  margin-right: auto;
  margin-left: auto;
  gap: 51px;
}
.testimonials-section .row .testimonial {
  padding: 30px;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  border-radius: 6px;
  border: 1px solid var(--border, #DDD);
  background: #FFF;
  color: var(--secondary-text, #777);
  line-height: 24px;
}
.testimonials-section .row .testimonial .quote {
  color: var(--Secondary-Text, #777);
  margin: 0;
  margin-bottom: 10px;
}
.testimonials-section .row .testimonial .credit {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  color: var(--hint-text, #AAA);
  font-size: 14px;
  float: left;
  margin: 0;
}
.testimonials-section .row .testimonial .rating {
  display: flex;
  align-items: flex-start;
  gap: 1px;
  float: right;
}
.testimonials-section .buttons.row {
  margin-top: 0;
  gap: 30px;
}

/* Hero Section */
.hero-section {
  display: flex;
  padding-top: 100px;
  padding-bottom: 100px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  flex-shrink: 0;
  background: var(--background-light, #F8F8F8);
}
.hero-section .screenshot-description {
  display: flex;
  align-items: flex-start;
}
.hero-section .screenshot-description .screenshot {
  margin-right: auto;
  margin-left: auto;
  width: 354px;
  height: 465px;
  border-radius: 6px;
  box-shadow: 0 10px 30px 0 rgba(0, 0, 0, 0.1);
  background-image: image-set(url("../img/screenshot-01.png") 1x, url("../img/screenshot-01@2x.png") 2x);
  background-color: lightgray;
  background-repeat: no-repeat;
  background-size: contain;
}
.hero-section .screenshot-description .description {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
}
.hero-section .screenshot-description .description .title {
  color: var(--primary-text, #444);
  font-size: 32px;
  font-weight: 700;
}
.hero-section .screenshot-description .description .description {
  color: var(--secondary-text, #777);
  font-size: 18px;
  line-height: 28px;
}

/* Screenshot Section */
.screenshot-section {
  display: inline-flex;
  align-items: flex-start;
  gap: 50px;
  margin-top: 100px;
  width: 100%;
}
.screenshot-section .screenshot {
  width: 339px;
  height: 191px;
  border-radius: 6px;
  box-shadow: 0 5px 25px 0 rgba(0, 0, 0, 0.15);
  margin: 0 auto;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: lightgray;
}
.screenshot-section .screenshot.screenshot-one {
  background-image: image-set(url("../img/screenshot-02.png") 1x, url("../img/screenshot-02@2x.png") 2x);
  margin-bottom: 30px;
}
.screenshot-section .screenshot.screenshot-two {
  background-image: image-set(url("../img/screenshot-03.png") 1x, url("../img/screenshot-03@2x.png") 2x);
}

/* Secondary Features Section */
.secondary-features-section {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  gap: 100px;
  margin-top: 100px;
  margin-bottom: 100px;
  width: 100%;
}
.secondary-features-section .title {
  color: var(--primary-text, #444);
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  line-height: 36px;
}
.secondary-features-section .row {
  margin-top: 100px;
}
.secondary-features-section .row .feature {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  flex: 1 0 0;
}
.secondary-features-section .row .feature .icon {
  width: 48px;
  height: 48px;
}
.secondary-features-section .row .feature .title {
  color: var(--primary-text, #444);
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  line-height: 30px;
}
.secondary-features-section .row .feature .description {
  color: var(--secondary-text, #777);
  text-align: center;
  line-height: 24px;
}
.secondary-features-section .description {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}
.secondary-features-section .description .title {
  color: var(--primary-text, #444);
  font-size: 24px;
  font-weight: 700;
  line-height: 30px;
}
.secondary-features-section .description .link {
  font-size: 18px;
  text-align: center;
}
.secondary-features-section .description .link a {
  text-decoration-line: underline;
  color: var(--blue, #036AAB);
}

/* Mobile landscape */
@media (min-width: 576px) {
  .col-sm {
    flex: 1 0 0;
  }
  .col-sm-6 {
    flex: 0 0 auto;
    width: 50%;
  }
  .header-section .video {
    width: 750px;
    height: 422px;
  }
  .header-section .video iframe {
    width: 750px;
    height: 422px;
  }
}
/* Tablet */
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
  .row {
    gap: 0;
  }
  .wrapper .button {
    width: 290px;
  }
  .wrapper .buttons {
    gap: 50px;
  }
  .header-section {
    height: 828px;
  }
  .header-section .video {
    width: 750px;
    height: 422px;
  }
  .header-section .video iframe {
    width: 750px;
    height: 422px;
  }
  .features-section {
    margin-top: 120px;
  }
  .features-section .row.top {
    border-bottom: 1px solid #ddd;
  }
  .features-section .row.top .feature {
    padding-bottom: 51px;
  }
  .features-section .row.bottom {
    margin-top: 0;
  }
  .features-section .row.bottom .feature {
    padding-top: 51px;
  }
  .features-section .row,
  .secondary-features-section .row {
    gap: 0;
  }
  .features-section .col-sm:first-child,
  .secondary-features-section .col-sm:first-child {
    padding-right: 51px;
    border-right: 1px solid #ddd;
  }
  .features-section .col-sm:last-child,
  .secondary-features-section .col-sm:last-child {
    padding-left: 51px;
    border-left: 1px solid #ddd;
  }
  .features-section .col-sm:nth-child(2),
  .secondary-features-section .col-sm:nth-child(2) {
    padding-left: 51px;
    padding-right: 51px;
  }
  .testimonials-section .row .testimonial .rating {
    float: left;
  }
  .hero-section .screenshot-description {
    gap: 0;
  }
  .hero-section .screenshot-description .screenshot {
    width: 339px;
    height: 445px;
  }
}
/* Tablet landscape */
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
  .hero-section .screenshot-description .screenshot {
    width: 457px;
    height: 600px;
  }
  .screenshot-section .screenshot {
    width: 457px;
    height: 257px;
  }
  #header {
    height: 928px;
  }
  .testimonials-section .row .testimonial .rating {
    float: right;
    margin-top: 4px;
  }
}
/* Desktop */
@media (min-width: 1400px) {
  .container {
    max-width: 960px;
  }
  .header-section .video {
    width: 960px;
    height: 540px;
  }
  .header-section .video iframe {
    width: 960px;
    height: 540px;
  }
  .features-section {
    margin-top: 240px;
  }
  .hero-section .screenshot-description .screenshot {
    width: 457px;
    height: 600px;
  }
  .screenshot-section .screenshot {
    width: 457px;
    height: 257px;
  }
}